export const updateIframeHeight = (queryString) => {
    const iframes = [
        ...document.querySelectorAll(queryString),
    ];
    if (iframes.length === 0)
        return Promise.resolve();
    window.addEventListener('message', (event) => {
        const iframe = iframes.find((i) => {
            try {
                return i.name === event.source.name;
            }
            catch (e) {
                return false;
            }
        });
        if (iframe) {
            try {
                // eslint-disable-next-line @typescript-eslint/no-unsafe-argument -- we’re in a try-catch
                const message = JSON.parse(event.data);
                switch (message.type) {
                    case 'set-height':
                        iframe.height = message.value;
                        break;
                    default:
                }
            }
            catch (e) {
                // do nothing
            }
        }
    });
    for (const iframe of iframes) {
        const src = (iframe.getAttribute('srcdoc') ?? '')
            .replace(/<gu-script>/g, '<script>')
            .replace(/<\/gu-script>/g, '<' + '/script>');
        iframe.setAttribute('srcdoc', src);
    }
    return Promise.resolve();
};
